<template>
  <async-container
    style="background: linear-gradient(225deg, #f1f1f1 0%, #0072ff 100%);"
    :loading="loading"
    color="white"
    fluid
  >
    <v-card
      width="24rem"
      max-width="94%"
      elevation="8"
      style="border-radius: 1rem"
      class="mx-auto"
    >
      <v-toolbar
        color="primary"
        dark
        class="d-flex justify-center align-center"
        flat
        dense
      >
        <div class="text-center">
          <p class="title my-0" style="line-height: 1.1rem">{{nome}}</p>
          <p class="subtitle-1 my-0" style="line-height: 1.1rem">Corretor de Imóveis</p>
        </div>
      </v-toolbar>
      <v-card-text class="text-center pt-2 pb-0">
        <v-img
          :src="$store.getters['ficha/avatar']"
          alt=""
          width="7.5rem"
          height="10rem"
          style="border-radius: .55rem"
          class="mx-auto elevation-3 mb-2"
        ></v-img>
        <v-chip
          color="primary"
          label
          small
          class="mb-3 justify-center"
          style="min-width: 7.5rem; font-weight: 500"
        >{{creci}}</v-chip>

        <p class="subtitle-2 mb-0" style="line-height: 1.1rem">Telefone comercial</p>
        <p class="body-1 mb-0" style="line-height: 1.1rem">{{telefone}}</p>
        <v-divider class="my-2"></v-divider>

        <p class="subtitle-2 mb-0" style="line-height: 1.1rem">E-mail principal</p>
        <p class="body-1 mb-0" style="line-height: 1.1rem">{{email.toLowerCase()}}</p>
        <v-divider class="my-2"></v-divider>

        <p class="subtitle-2 mb-0" style="line-height: 1.1rem">Endereço Comercial</p>
        <p class="body-2 mb-0" style="line-height: 1.1rem">{{endereco}}</p>
        <v-divider class="my-2"></v-divider>
      </v-card-text>
      <v-card-actions class="justify-center pt-0">
        <v-btn small to="/" outlined color="primary">Voltar</v-btn>
      </v-card-actions>
    </v-card>
  </async-container>
</template>

<script>
  import AsyncContainer from '@/components/AsyncContainer';
  import AppWebClient from '@/http/AppWebClient';
  import CreciHelper from "@/helpers/CreciHelper";
  export default {
    name: 'Perfil',
    components: {AsyncContainer},
    data: () => ({
      loading: true,
      webClient: new AppWebClient(),
      perfil: null,
    }),
    methods: {
      async loadData() {
        try {
          const perfil = await this.webClient.getPerfil();
          this.perfil = perfil;
        } catch (e) {
          await this.$router.push('/');
        } finally {
          this.loading = false
        }
      },
    },
    computed: {
      nome() {
        return this.$store.state.ficha.cadastro ? this.$store.state.ficha.cadastro.nome : '';
      },
      telefone() {
        if (!this.perfil || !this.perfil.telefone) return '';
        return '(' + this.perfil.telefone.substr(0,2) + ') ' + this.perfil.telefone.substr(2);
      },
      email() {
        return this.perfil && this.perfil.email ? this.perfil.email : '';
      },
      endereco() {
        return this.perfil && this.perfil.endereco ? this.perfil.endereco : '';
      },
      creci() {
        return this.$store.state.ficha.cadastro ? 'CRECI-' + CreciHelper.idParaUf(this.$store.getters['session/payload'].regional) + ' ' + this.$store.state.ficha.cadastro.creci : '';
      },
    },
    created() {
      this.loadData();
    },
  }
</script>
